<template>
  <div>
    <div id="container">
      <h1 align="center" class="header">拼图九宫格</h1>
      <ul id="box">
        <!-- /* 水平位置left: 取余3就是第几列   val%3 就是当前的列  -val%3*100+px 就是 要移动的横向 */ -->
        <!--      垂直位置top     除以三向下取整               -当前所在行*100 -->
        <!-- 如果val的值是空的 val没有图片 -->
        <!-- [3,4,1,0,2,7,6,''] -->
        <li
          v-for="(val,index) in list"
          @click="move(index)"
          :style="val===''?{backgroundImage:'none'}:{backgroundPosition:-100*(val%3)+'px -'+100*Math.floor(val/3)+'px'}"
          :key="val"
        ></li>
      </ul>
      <div class="refresh" @click="init">刷新</div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    init() {
      let arr = [];
      for (var i = 0; i < 8; i++) {
        arr.push(i);
      }
      arr.push("");
      arr.sort(function() {
        return Math.random() - 0.5;
      });
      this.list = arr;
    },
    move(index) {
      let current = this.list[index],
        left = this.list[index - 1],
        right = this.list[index + 1],
        top = this.list[index - 3],
        bottom = this.list[index + 3];
      if (left === "" && index % 3 != 0) {
        this.$set(this.list, index - 1, current);
        // Vue.set(this.list,index-1,curret)
        this.$set(this.list, index, "");
      } else if (right === "" && index % 3 != 2) {
        this.$set(this.list, index + 1, current);
        this.$set(this.list, index, "");
      } else if (top === "") {
        this.$set(this.list, index - 3, current);
        this.$set(this.list, index, "");
      } else if (bottom === "") {
        this.$set(this.list, index + 3, current);
        this.$set(this.list, index, "");
      }
      this.pass();
    },
    // 验证是否闯关成功
    pass() {
      // 如果第九个是空
      if (this.list[8] === "") {
        // 让ispass等于 截取这个数组从0-7的元素,每一个的内容都等于索引，所有的都符合ele==index就返回true 如果一个不行就返回false
        let isPass = this.list.slice(0, 8).every(function(element, index, arr) {
          // console.log(elem,index);
          return element === index;
        });
        if (isPass) {
          setTimeout(() => {
            alert("闯关成功");
            this.init();
          }, 1000);
        }
      }
    }
  },
  created() {
    this.init();
    let obj = { title: "游戏", color: "game" };
    this.$emit("changeNav", obj);
  }
};
</script>
<style scoped>
#container {
  width: 312px;
  height: 312px;
  border-radius: 0.5rem;
  overflow: hidden;
}

.header {
  font-family: "幼圆";
  font-weight: bolder;
}
#box {
  width: 312px;
  height: 312px;
  background: #ccc;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#box li {
  list-style: none;
  width: 100px;
  height: 100px;
  float: left;
  margin: 2px;
  background: url(raven.png) 0 0 no-repeat;
  cursor: pointer;
  color: greenyellow;
}
.refresh {
  position: absolute;
  display: block;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  line-height: 0.5rem;
  width: 1rem;
  height: 0.5rem;
  background: #f00;
  color: aliceblue;
}
</style>